import React from 'react';
import './App.css';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import Rank from './components/Rank/Rank';
import Particles from 'react-particles-js';
import { particlesConfig } from './components/particlesConfig'
import Clarifai from 'clarifai'
import FaceRecognition from './components/FaceRecognition/FaceRecognition'
import Signin from './components/Signin/Signin';
import Register from './components/Register/Register';

const app = new Clarifai.App({
  apiKey: 'c9d37d7d348d4a75a95895ffb057800f'
});

class App extends React.Component {

  constructor() {
    super();
    this.state = {
      input: '',
      imageUrl: '',
      boxs: [],
      router: 'signin',
      isSignedIn: false
    }
  }

  calcFaceLocation = (data) => {
    return data.outputs[0].data.regions;
  }

  calcBoxs = (data1) => {
    var returnBoxs = [];
    data1.forEach(function(item, index) {
      const clarifaiFace = item.region_info.bounding_box;//data[0].region_info.bounding_box[index];
      const image = document.getElementById('inputimage')
      const width = Number(image.width);
      const height = Number(image.height);
      returnBoxs.push(
        {
          leftCol: clarifaiFace.left_col * width,
          topRow: clarifaiFace.top_row * height,
          rightCol: width - clarifaiFace.right_col * width,
          bottomRow: height - clarifaiFace.bottom_row * height
        }
      );
      //console.log(returnBoxs);
    })
    //console.log(returnBoxs);
    return returnBoxs;
  }

  displayFaceBox = (boxs) => {
    const t1 = this.calcBoxs(boxs);
    this.setState({boxs: t1});
    //console.log(this.state.boxs);
  }

  onInputChange = (event) => {
    this.setState({input: event.target.value});
  }

  onButtonSubmit = () => {
    this.setState({imageUrl: this.state.input});
    app.models.predict(Clarifai.FACE_DETECT_MODEL, this.state.input)
    .then( response => this.displayFaceBox(this.calcFaceLocation(response)))
    .catch(err => console.log(err));
  }

  onRouteChange = (router) => {
    if(router === 'signout') 
    {
      this.setState({isSignedIn: false});
    }
    if(router === 'home') 
    {
      this.setState({isSignedIn: true});
    } 
    this.setState({router: router});
  }

  render() {
    return (
      <div className="App">
        <Particles className='partices' params={particlesConfig} />
        <Navigation  isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange} />
        { 
          this.state.router === 'home'?
            <div>
              <Logo />
              <Rank />
              <ImageLinkForm 
                onInputChange={this.onInputChange} 
                onButtonSubmit={this.onButtonSubmit} 
              />
              <FaceRecognition boxs={this.state.boxs} imageUrl={this.state.imageUrl}/> 
            </div> :
          (
            this.state.router === 'signin'? 
            <Signin onRouteChange={this.onRouteChange} /> : 
            (
              this.state.router === 'signout'?<Signin onRouteChange={this.onRouteChange} /> 
              : <Register onRouteChange={this.onRouteChange} />
            )
              
          )  
          
        }
      </div>
    );
  }
}

export default App;
